import React from 'react';
import { createRoot } from 'react-dom/client'; // Importar createRoot

// IMPORTANTE: SOLO DEJA LA LNEA DE TU ARCHIVO CSS REAL, QUE ES 'styles.css'
// import './index.css'; // <--- ELIMINA ESTA LNEA (o comntala)
import './styles.css'; // <--- ESTA ES LA CORRECTA

import App from './App';
import { AuthProvider } from './components/Auth/AuthProvider'; // NO OLVIDES RE-AGREGAR AuthProvider!

// Obtener el elemento raz
const container = document.getElementById('root');
// Crear una raz de React 18
const root = createRoot(container);

// Renderizar la aplicacin con la nueva API
root.render(
  <React.StrictMode>
    {/* ENVUELVE App CON AuthProvider AQU! */}
    <AuthProvider>
      <App />
    </AuthProvider>
  </React.StrictMode>
);

